import Vue from 'vue'
import { Home, transactionData, loginfree } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import Cryptojs from 'crypto-js'
import { Swipe, SwipeItem, Search, Toast, NoticeBar, DropdownMenu, DropdownItem } from 'vant'
Vue.use(Swipe).use(SwipeItem).use(Search).use(Toast).use(NoticeBar).use(DropdownMenu).use(DropdownItem)
export default {
  components: {},
  data () {
    return {
      languageStatus: '',
      value1: 0,
      value2: 'a',
      option1: [
        { text: '中文', value: 0 },
        { text: 'Engish', value: 1 }
      ],
      url: this.GLOBAL.domainName,
      navList: [
        'http://192.168.1.200/public/upload/2020-05-12/1589266987744595.jpg',
        'http://192.168.1.200/public/upload/2020-05-12/1589266987725561.jpg',
        'http://192.168.1.200/public/upload/2020-05-12/1589266988971792.jpg'
      ],
      value: '',
      icon: [
        {
          id: '1',
          title: '法币',
          icon: '/public/upload/2020-06-10/1591754465342303.png',
          path: '/currency'
        },
        {
          id: '2',
          title: '币币',
          icon: '/public/upload/2020-06-10/1591754501753192.png',
          path: '/coin'
        },
        {
          id: '3',
          title: '合约',
          icon: '/public/upload/2020-06-10/1591754525183111.png',
          path: '/contract'
        },
        {
          id: '4',
          title: '杠杆',
          icon: '/public/upload/2020-06-10/1591754554736959.png',
          path: '/lever'
        },
        {
          id: '5',
          title: '新币',
          icon: '/public/upload/2020-06-10/1591754587450491.png',
          path: '/presale'
        },
        {
          id: '6',
          title: '投币',
          icon: '/public/upload/2020-06-10/1591754607642559.png',
          path: '/vote'
        },
        {
          id: '7',
          title: '矿池',
          icon: '/public/upload/2020-06-10/1591754628549670.png',
          path: '/currencyInterest'
        },
        {
          id: '8',
          title: '拍卖',
          icon: '/public/upload/2020-06-10/1591754694585842.png',
          path: '/auction'
        }
      ],
      currency: [], // 推荐数据、
      exchange: [], // 交易数据
      fall: [], // 涨
      rise: [], // 跌
      num: 0, // 切换判断
      noticeList: [
        {
          content: '苹果也疯狂！iPhone XR来到小米价，刷新售价底线'
