        },
        {
          content: '55号电池一出”，充电宝没用了，所有手机通用，行千里不关机'
        },
        {
          content: '45号电池一出”，充电宝没用了，所有手机通用，行千里不关机'
        }
      ],
      // 七天免登录
      datas: {
        tel: ''
      },
      // 。。。。。。。。。。。。。。。。。。。
      // 。。。。。。。。。。。。。。。。。。。
      animate: false
    }
  },
  created () {
    // this.voteList()
    // localStorage.setItem('objStr1', '0000')
    // sessionStorage.setItem('objStr', '9999')
    // var a = sessionStorage.getItem('objStr')
    // var b = localStorage.getItem('objStr1')
    // console.log(a, 'sessionStorage缓存', b, 'localStorage缓存')
    this.homeIndex()
    this.transactionList()
    this.sevenDays()
    setInterval(this.showNotice, 3000)
    this.getLanguageStatus()
  },
  methods: {
    changeLan (value) {
      console.log(value)
      this.$changeLan()
      console.log(localStorage.getItem('language'))
      console.log(this.$store.state)
      this.getLanguageStatus()
    },
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    // 七天免登录
    sevenDays () {
      var that = this
      // that.datas.tel = '18706210686'
      console.log('取值u电话号码,', localStorage.getItem('userTel'))
      that.datas.tel = localStorage.getItem('userTel')
      var data = objKeySort(that.datas)
      var token = sgin(data)
      console.log(token, data)
      that.datas.token = token
      console.log(that.datas, 'this.datas')
      loginfree(that.datas).then(res => {
        console.log(res, '七天成功')
        sessionStorage.setItem('u_id', res.data.u_id)
        sessionStorage.setItem('usdt_available_balance', res.data.usdt_available_balance)
        sessionStorage.setItem('transaction_password', res.data.transaction_password)
      }).catch((err) => {
        console.log(err, '七天失败')
        sessionStorage.setItem('u_id', '')
        sessionStorage.setItem('usdt_available_balance', '')
        sessionStorage.setItem('transaction_password', '')
        delete this.addCoin.token
      })
    },
    toMarket () {
      this.$router.push({
        path: '/market',
        query: {
        }
      })
    },
    toNoticeList () {
      this.$router.push({
        path: '/notice',
        query: {
        }
      })
    },
    showNotice () {
      var that = this
      that.animate = true
      setTimeout(() => {
        that.noticeList.push(that.noticeList[0])
        that.noticeList.shift()
        that.animate = false
      }, 2200)
    },
    onSearch (val) {
      Toast(val)
    },
    onCancel () {
      Toast('取消')
    },
    navSwitch (index) {
      this.num = index
    },
    homeIndex () {
      const Message = 'type=indexMDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
      const key = 'MDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
      const hash = Cryptojs.HmacSHA256(Message, key).toString()
      console.log(hash)
      var parameter = { type: 'index', token: hash }
      // var aa = { type: 'index' }
      var that = this
      Home(parameter).then(res => {
        console.log(res)
        that.navList = res.data.banner
        that.icon = res.data.icon
      }).catch((err) => console.log(err))
    },
    transactionList () {
      const Message = 'type=dataMDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
      const key = 'MDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
      const hash = Cryptojs.HmacSHA256(Message, key).toString()
      console.log(hash)
      var parameter = { type: 'data', token: hash }
      // var aa = { type: 'index' }
      var that = this
      transactionData(parameter).then(res => {
        console.log(res, 'ppppppppp')
        that.currency = res.data.currency // 推荐数据、
        that.exchange = res.data.exchange // 交易数据
        that.fall = res.data.fall // 涨
        that.rise = res.data.rise // 跌
      }).catch((err) => console.log(err))
    },
    toPersonal () {
      this.$router.push({
        path: '/personalIndex',
        query: {
        }
      })
    },
    /**
     * 导航点击前往
     */
    toDetails (url) {
      this.$router.push({
        path: url,
        query: {
        }
      })
    }
  },
  mounted () {
  }
}
</script>
